*{
    box-sizing: border-box;
}

body{
    margin:auto;
    text-align: left;
    font-family: Arial, Helvetica, sans-serif;
    min-height: 100vh;
    position: relative;
}

header{
    background-color: #666;
    padding: 30px;
    text-align: center;
    font-size: 18px;
    color: white;
}

/* Navigation Bar Styling */
.nav-bar{
    position: -webkit-sticky; /* Safari */
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: #333;
    width:100%;
    display:flex;
    flex-wrap:wrap;
    justify-content: center;
}

.nav-item{
    border-right:1px solid #bbb;
    border-left:1px solid #bbb;
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

.nav-item:hover:not(.nav-item-active){
    background-color: #111;
}

.nav-item-active {
    background-color: #008081;
}


/* Section */
section{
    font-size: 16px;
    padding-top:10px;
    max-width: 1280px; 
    margin:auto;
    padding-bottom:50px;
}

div.content{
    width:100%;
    text-align:justify;
    display: flex;
    flex-wrap:wrap;
    justify-content: center;
    margin-top:50px;
}
div.item{
    padding:10px;
    margin:auto;
    max-width:600px;
}

/* Images */
.row {
    display: -ms-flexbox; /* IE 10 */
    display: flex;
    -ms-flex-wrap: wrap; /* IE 10 */
    flex-wrap: wrap;
    padding: 0 4px;
}

/* Create two equal columns that sits next to each other */
.column {
    -ms-flex: 33%; /* IE 10 */
    flex: 33%;
    padding: 0 4px;
}

.column img {
    margin-top: 8px;
    vertical-align: middle;
}

img{
    width:100%;
    max-width: 100%;
    image-orientation: from-image;
    border-radius: 5px;
    cursor: pointer;
}

img.project_img{
    max-width:600px;
    margin:auto;
    width:100%;
}
/* Movie Posters Page */
div.posterGrid{
    display:flex;
    flex-wrap:wrap;
    text-align: center;
    justify-content: center;
    align-items: center;
}

div.poster{
    display:block;
    max-width:200px;
    width:100%;
    padding:2px;
}

img.posterImg{
    border:1px solid black;
}

img.posterImg:hover{
    background-color:black;
    opacity: 0.5;
}

/* The Modal (background) */
div.lightbox {
    display: flex;/* Center middle div*/
    position: fixed; /* Stay in place */
    z-index: 1001; /* Sit on top */ /* Set high to be above ScrollUpButton */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.9); /* Black w/ opacity */
    justify-content: center;
}
  
div.LBDisplay{
    margin: auto;
    display: block;
    background-color: white;
    display:flex;
    height:auto;
    overflow:hidden;
    max-width: 70vw;
    position: relative;
}
  

@media screen and (max-width:576px){
    div.LBDisplay{
        flex-direction: column;
    }
}

/* Modal Content (Image) */
.LBImage {
    margin: left;
    display: block;
    width: 300px;
    height:auto;
    padding:10px;
}
  
.LBInfo{
    margin:left;
    display:block;
    padding:10px;
}

#LBPosterTitle{
    font-size: xx-large;
    font-weight: bold;
    padding:1px;
}
#LBPosterDirector{
    font-size:larger;
    color:gray;
    padding: 1px;
}
#LBPosterRating{
    padding:1px;
}
#LBPosterPlot{
    padding:1px;
}

/* For Videos Page */
h1{
    text-align: center;
}

div.videoRow{
    display: flex;
    flex-direction: row;
    justify-content: center;
}

@media screen and (max-width:1000px){
    div.videoRow{
        flex-direction: column;
    }
}


.videoItem{
    display:block;
    width:100%;
    padding:30px;
}

.container {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 56.25%;
    display:block;
    z-index: 0;
}
.video {
    position: absolute;
    top: 0;
    left: 0;
    width:100%;
    height:100%;
    z-index: 0;
}

/* Feedback */

/* Fields */
div.FeedbackLabel{
    padding:10px;
    text-align:left;
}
textarea{
    -moz-box-shadow: inset -3px -3px 10px #eee;
    -webkit-box-shadow: inset -3px -3px 10px #eee;
    box-shadow: inset -3px -3px 10px #eee;
    border-radius: 15px;
    width: 100%;
    padding: 5px;
 }

/* Select */
/* https://www.filamentgroup.com/lab/select-css.html */
select{
	display: block;
	font-size: 16px;
	font-family: sans-serif;
	font-weight: bold;
	color: #444;
	line-height: 1.3;
	padding: .6em 1.4em .5em .8em;
	width: 100%;
	max-width: 100%;
	box-sizing: border-box;
	margin: 0;
	border: 1px solid #aaa;
	box-shadow: 0 1px 0 1px rgba(0,0,0,.04);
	border-radius: .5em;
	-moz-appearance: none;
	-webkit-appearance: none;
	appearance: none;
	background-color: #fff;
	background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'),
	  linear-gradient(to bottom, #ffffff 0%,#e5e5e5 100%);
	background-repeat: no-repeat, repeat;
	background-position: right .7em top 50%, 0 0;
	background-size: .65em auto, 100%;
}
.select-css::-ms-expand {
	display: none;
}
.select-css:hover {
	border-color: #888;
}
.select-css:focus {
	border-color: #aaa;
	box-shadow: 0 0 1px 3px rgba(59, 153, 252, .7);
	box-shadow: 0 0 0 3px -moz-mac-focusring;
	color: #222;
	outline: none;
}
.select-css option {
	font-weight:normal;
}


 /* Button */
 input[type=submit] {
    -moz-box-shadow: inset -3px -3px 10px #cc870a;
    -webkit-box-shadow: inset -3px -3px 10px #cc870a;
    box-shadow: inset -3px -3px 10px #cc870a;
    background-color: #f7b31c; /* Fill color */
    color: white; /* Text color */
    border-radius: 15px; /* Rounded button edges */
    font-style: italic; /* Italicize text */
    padding: 10px 30px; /* Distance between text and border */
    font-weight: bold;
 }

 div.submitButton{
     padding-top:10px; 
     text-align:center;
 }

 div.FirebaseScroll{
    overflow-y:scroll;
    overflow-wrap: break-word;
    width:100%;
    background-color:#666;
    max-height: 80vh;
    border-radius:25px;
}


div.FeedbackItemContainer{
    padding:10px;
    padding-right:50px;
}

ul.FeedbackList{
    list-style-type: none;
}

li.FeedbackItem{
    background-color: white;
    width:100%;
    overflow-x:wrap;
    max-width: 300px;
    border:10px;
    border-color: #666;
    border-radius:25px;
    padding:10px;
}

div#itemTime{
    color:red;
    font-size: x-small;
    padding-left:5px;
}

div#itemName, div#itemMessage{
    padding-top:5px;
    padding-left:5px;
}

div#itemEmail, div#itemDescription{
    color:#444;
    font-size: small;
    /* padding:5px; */
    padding-left: 15px;
}


/* Footer */
footer{
    box-sizing:border-box;
    background-color: #111;
    color:white;
    padding:10px;
    margin:auto;
    text-align: center;
    bottom:0;
    position: absolute;
    width:100%;
}

footer a{
    padding:5px;
    color:white;
}